import React, { useState, useEffect } from 'react';
import { PeopleTable } from '../PeopleTable/PeopleTable';
import './PeoplePage.scss';

export const PeoplePage = () => {
  const [people, setPeople] = useState([]);
  
  useEffect(() => {
    getPeople()
      .then(setPeople)
    console.log(people);
  }, []);

  return (
    <>
      <h2>People page</h2>
      <PeopleTable people={people} />
    </>
  );
}

async function getPeople() {
  const response = await fetch(`https://mate-academy.github.io/react_people-table/api/people.json`);

  const people = await response.json();


  return people.map(person => ({
    ...person,
    mother: people.find(mother => mother.name === person.motherName),
    father: people.find(father => father.name === person.fatherName),
  }))
}
